@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css?family=Orbitron');

html {
    scroll-behavior: smooth;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 100%;
}

.typed {
  overflow: hidden;
    white-space: nowrap;
    border-right: 2px solid;
    width: 0;
    animation: typing 2.5s steps(30, end) forwards, blinking 1s infinite;
    color: white;
    font-family: Orbitron;
}

@keyframes typing {
  from {
      width: 0
  }

  to {
      width: 100%
  }
}

@keyframes blinking {
  0% {
      border-color: transparent
  }

  50% {
      border-color: lawngreen
  }

  100% {
      border-color: transparent
  }
}